@media(max-width: 900px){
    .auth {
        margin: 0 auto!important;
    }
    .form {
        width: 90%!important;
        margin: 0 auto!important;
    }
    .title{
        text-align: center;
    }
    .logo{
        margin: 5% 15%;
    }
}

.auth {
    margin-top: 2.5rem;
    margin-left: 20%;
}
.input{
    margin-top: 1.5rem!important;
    background: var(--primary-white) !important;
}
.title,
.icon_list {
    margin-top: 2.5rem;
}
.button_forget{
    display: flex;
    justify-content: center;
}
.form {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 60%;
    margin-top: 2.5rem;
}

.icon_list {
    width: 15rem;
    justify-content: space-between;
    display: flex;
    flex-wrap: wrap;
    padding-inline-start: 0;
    margin-block-end: 0;
}

.login_icon {
    border-radius: 100%;
    width: 27%;
    height: 27%;
}

.form_item {
    margin-top: 1.25rem;
}

.form_item_button {
    margin-top: 1.75rem;
}

.after_list {
    justify-content: space-between;
    display: flex;
    align-items: center;
    margin-top: 0.75rem;
}

.list_text {
    color: var(--primary-black);
}
.captcha{
    position: absolute;
    top:25vh;
}