@media (max-width: 960px){
    .actual_block{
        flex-direction: column;
        padding: 1rem!important;
    }
    .like_container{
        flex-direction: row!important;
        width: 100% !important;
    }
    .info_container{
        width: 100% !important;
    }
    .link_block{
        width: 100% !important;
    }
}

.actual_block{
    display: flex;
    gap:1.25rem;
    margin-bottom: 2.5rem;
    padding: 2.5rem 3rem;
}
.actual_block:hover{
    background: var(--accent-light-yellow);
    border-radius: 1.25rem;
    cursor: pointer;
}

.img_container {
  display: flex;
  justify-content: center;
  width: 6rem;
  height: 5rem;
  gap: 1.25rem;
}
.logo {
  flex-shrink: 1;
  max-width: 100%;
  max-height: 100%;
}
.info_container{
    width: 60%;
    height: 100%;
    display: flex;
    flex-direction: column;
}
.stats_num {
    margin-left: 0.5rem !important;
}

.description_block, .title_block{
    overflow: hidden;
    text-overflow: ellipsis!important;
    display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}
.description{
    color: var(--primary-dark-grey)!important;
}
.stats{
    display: flex;
    gap:1.25rem;
}
.stats_part{
    display: flex;
    align-items: center;
    gap:0.75rem;
}
.badges{
    margin-top: 1rem;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}
.badge_row {
    display: flex;
    gap: 0.5rem;
    flex-wrap: wrap;
}
.like_container {
    justify-content: center;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    width: 35%;
    gap: 1.25rem;
}
.link_block{
    width: 75%;
    display: flex;
    gap:1.25rem;
}
.button{
    font-size: 1.125rem!important;
    font-style: normal!important;
    font-weight: 500!important;
    line-height: 1.5rem!important;
    padding: 0.75rem!important;
}
.stats_blocks{
    display: flex;
    gap:0.75rem;
    align-items: center;
}
.stats_block{
    display: flex;
    gap:0.25em;
}
.category{
    transition: 0ms!important;
}
.tag{
    transition: 0ms!important;
}
.actual_block:hover .category{
    background: var(--secondary-blue);
}
.actual_block:hover .tag{
    transition: 0ms!important;
    background: var(--secondary-pink);
}
.img {
  border-radius: 100%;
  width: 100%;
  height: 100%;
  object-fit: cover;
}


